@font-face {
    font-family: comic;
    font-style: normal;

    /*! See: /static/fonts/NOTICE.txt */
    src:
        local("Comic Sans MS"),
        url("/static/fonts/comic.woff2?v=0") format("woff2");
}

:root,
[tooltip]::after {
    font-family: comic, serif;
}

.openmoji {
    font-family: OpenMojiColor, comic, serif;
}

@font-face {
    font-family: comic-mono;
    font-style: normal;

    /*! See: /static/fonts/NOTICE.txt */
    src: url("/static/fonts/comicmono.woff2?v=0") format("woff2");
}

pre *,
.pre,
.mono-font {
    font-family: comic-mono, ui-monospace, monospace;
}
